resolve<IQCloudApi>(IQCloudApi);\nconst executor = container.resolve<IUseCaseExecutor>(IUseCaseExecutor);\nconst dialog = container.resolve<IDialog>(IDialog);\nconst showLog = ref(false);\nconst logs = ref(Array<string>());\nconst props = defineProps<IStep>();\nconst hosts = ref(Array<IDeployedPackageUrl>());\nconst customSteps = [\"Pipeline Started\", \"Deployment Completed\"]\nconst isUsingIAM = ref(false);\n\n\nasync function changeShowLog() {\n  if (props.status == \"waiting\" || (props.name == \"Deployment Completed\" && props.status != 'success'))\n    return\n\n  if (!showLog.value) {\n    await executor.execute(\n      async () => {\n        let resp: IlistExportJobStepLogsResponse = await qcloudApi.listExportJobStepLogs(props.ID);\n        if (resp.logs) {\n          logs.value = resp.logs\n        }\n      },\n      { loading: true }\n    );\n  }\n\n  if (props.name == \"Deployment Completed\"){\n    hosts.value = await getDeploymentResult();\n  }\n\n  showLog.value = !showLog.value\n}\n\nfunction calculateRowHeight() {\n  if (!showLog.value)\n    return \"40px\";\n\n  switch (props.name) {\n    case \"Pipeline Started\":\n      return \"109px\";\n    case \"Deployment Completed\":\n      return \"152px\";\n    default:\n      return \"126px\";\n  }\n}\n\nasync function getDeploymentResult() {\n  let hosts = Array<IDeployedPackageUrl>();\n  await executor.execute(async () => {\n    const application = await qcloudApi.getApplication(props.appID);\n\n    if(props.cloudProvider == \"azure\" && application.cloud?.azure){\n      hosts.push({url: application.cloud.azure.dnsEndpoint!, name: application.name, \"type\": \"application\" })\n      return\n    }\n\n    if (props.cloudProvider == \"plateauGCP\" && application.cloud?.plateauGCP){\n      const jobs = await qcloudApi.getExportJobsByGroupId({ groupID: props.groupID }).then(res => res.jobs);\n\n      const buildPackages = jobs.filter(e => e.layer == 1).map(e => (e.jobData as IBuildJobData).package);\n\n      const gcpHosts = application.cloud.plateauGCP.hosts\n      if (!gcpHosts) return;\n\n      hosts.push({name:'ui', type: 'application', url:`http://${gcpHosts.find(e => e.startsWith('ui.'))}`  })\n\n      buildPackages.forEach(buildPack => {\n        const url = gcpHosts.find(gcpHosts => gcpHosts.startsWith(`${buildPack.name}.`))\n        if(url)\n          hosts.push({ name: buildPack.name, type: buildPack.type, url: `http://${url}/swagger-ui.html#/`})\n      })\n\n      isUsingIAM.value = gcpHosts.some(hosts => hosts.startsWith('identity-provider.'));\n    }\n  }, { loading: true });\n\n  return hosts;\n}\n\nfunction openInNewTab(url: string) {\n    window.open(url, '_blank', 'noreferrer');\n}\n\nfunction openPlaygroundUserCredentialsDialog() {\n    dialog.showDialog(\n        defineAsyncComponent(() => import(`../userCredentialsDialog.vue`)),\n        { closable: true, title: \"Playground User Credentials\", width: \"700px\", height: \"450px\" }\n    );\n}\n</script>\n<template>\n  <div class=\"step-row\" :style=\"{ background: (showLog ? '#EEEEEE' : '#FFFFFF'), height: calculateRowHeight() }\">\n    <span class=\"step-icon\" @click=\"changeShowLog()\">\n      <i id=\"play-icon\" class=\"mdi mdi-play\" v-if=\"!showLog\"></i>\n      <i id=\"play-icon\" class=\"mdi mdi-menu-down\" v-else></i>\n    </span>\n    <span style=\"margin-right: 4px;\">\n      <i class=\"mdi mdi-timer-sand\" v-if=\"props.status == 'waiting'\"></i>\n      <i class=\"mdi mdi-arrow-up-circle-outline\" v-else-if=\"props.status == 'running'\"></i>\n      <i class=\"mdi mdi-check-circle-outline\" v-else-if=\"props.status == 'success'\"></i>\n      <i class=\"mdi mdi-close-circle-outline\" v-else-if=\"props.status == 'failed'\"></i>\n    </span>\n    <span>{{ props.name }}</span>\n    <div style=\"background-color: white; margin-top: 8px;\">\n      <div class=\"log-container\" v-if=\"showLog && !customSteps.includes(props.name)\">\n        <span class=\"log-text\" v-for=\"log in logs\">{{ log }}</span>\n      </div>\n    </div>\n    <div class=\"pipeline-started-container\" v-if=\"showLog && props.name == 'Pipeline Started'\">\n      <span  class=\"pipeline-started-text\">\n        You can see the pipeline status by going to this link.\n        <a href=\"http://jenkins.rally.softtech/job/deploy-gke-V2-3-0/\" target=\"_blank\" rel=\"noreferrer\"> GO TO LINK</a>\n      </span>\n    </div>\n    <div v-if=\"showLog && props.name == 'Deployment Completed'\" class=\"deployment-completed-container\">\n      <div class=\"grid-container\">\n        <div class=\"grid-item\" v-if=\"isUsingIAM\">\n          <span class=\"deployment-complete-info-btn\" @click=\"openPlaygroundUserCredentialsDialog\">\n            <i style=\"display: flex;flex-direction: column;justify-content: center;align-items: center;gap: 10px;\" class=\"mdi mdi-card-account-details-outline\"></i>\n                <span>\n                    Playground User Credentials\n                </span>\n          </span>\n        </div>\n        <div v-for=\"host in hosts\">\n          <div class=\"grid-item\" style=\"padding-top: 12px;\">\n            <span class=\"deployment-complete-info-btn\" style=\"background: white;\" @click=\"openInNewTab(host.url)\">\n              <i style=\"display: flex;flex-direction: column;justify-content: center;align-items: center;gap: 10px;\" class=\"mdi mdi-link-variant\"></i>\n              <span :href=\"host.url\" target=\"_blank\" rel=\"noreferrer\">{{ host.name.toLocaleUpperCase() }} {{ host.type== \"application\" ? \"Application\" : \"Service\" }}</span>\n            </span>\n          </div>\n        </div>\n        <tr>\n        </tr>\n      </div>\n    </div>\n  </div>\n</template>\n<style scoped>\n.step-row {\n  display: inline-block;\n  width: 1302px;\n  height: 40px;\n  padding-top: 8px;\n  padding-bottom: 8px;\n  /* UI Properties */\n  background: #FFFFFF 0% 0% no-repeat padding-box;\n  opacity: 1;\n}\n\n.step-icon {\n  margin-left: 20px;\n  width: 32px;\n  float: left;\n}\n\n#play-icon {\n  color: #BCBCBC;\n  padding-top: 12px;\n  padding-bottom: 12px;\n  cursor: pointer;\n}\n\n.log-container {\n  display: block;\n  margin-left: 52px;\n  height: 88px;\n  width: 883px;\n  overflow: auto;\n}\n\n.log-container::-webkit-scrollbar {\n  display: none;\n}\n\n.log-text {\n  display: block;\n  width: 883px;\n  font-size: smaller;\n  font-weight: 600;\n  font-family: monospace;\n}\n\n.pipeline-started-container {\n  align-content: center;\n  display: grid;\n  height: 69px;\n  margin-top: 8px;\n  padding-left: 20px;\n  background: white;\n}\n\n.deployment-completed-container {\n  height: 114px;\n  margin-top: 8px;\n  padding-left: 20px;\n  background: white;\n}\n\n.pipeline-started-text {\n  display: block;\n  width: 883px;\n  font: normal normal normal 14/20px Roboto;\n  letter-spacing: 0px;\n  color: #616161;\n  padding-left: 30px;\n}\n\n\n.mdi-timer-sand {\n  color: #A1A1A1;\n}\n\n.mdi-arrow-up-circle-outline {\n  color: #449DD1;\n}\n\n.mdi-check-circle-outline {\n  color: #149911;\n}\n\n.mdi-close-circle-outline {\n  color: #DF2935;\n}\n\n.deployment-complete-info-btn {\n  display: inline-flex;\n  height: 48px;\n  padding: 8px 12px;\n  align-items: center;\n  gap: 4px;\n  flex-shrink: 0;\n  cursor: pointer;\n  border-radius: 4px;\n  background: var(--grey-lighten-4, #EEE);\n}\n\n.deployment-complete-info-btn > i > span {\n  color: var(--text-color, #212121);\n  text-align: center;\n  font-family: Roboto;\n  font-size: 16px;\n  font-style: normal;\n  font-weight: 500;\n  line-height: normal;\n  text-transform: capitalize;\n}\n\n.grid-container {\n  display: grid;\n  grid-template-columns: auto auto auto;\n  justify-content: start;\n  align-items: center;\n}\n\n.grid-item {\n  padding: 15px;\n}\n</style>\n"],"sourceRoot":""}]);
